import React, { useEffect, useState } from 'react'
import Popup from '../Popup'
import './style.css'

const Cookies = (props: any) => {

  const [cookie_popup, setcookie_popup] = useState(navigator.cookieEnabled)


  useEffect(() => {
    console.log('Cookies Enabled ?', cookie_popup);

  }, [])


  return (
    <>

      {!cookie_popup &&
        <Popup
          popup_body={
            <p>
              Oops Cookies are Not Enabled.. <br /> Please Enable in your browser setting's.
              <br />And Please Refresh the page after enabling it.
            </p>
          }
          confirmClick={() => {
            if (navigator.cookieEnabled) {
              setcookie_popup(false)
            }
          }}
          cancelClick={() => {
            setcookie_popup(false)
          }}
        />
      }

    </>
  )
}

export default Cookies